footer{
    background-color: var(--color-primary);
    margin-top: 5rem;
    padding: 3rem 0;
    color: var(--color-white);
    text-align: center;
    font-size: .9rem;
}
footer a{
    color: var(--color-bg);
}
.footer-logo{
    font-size: 2rem;
    font-weight: 600;
    margin-bottom: 1rem;
    display: inline-block;
}
.links{
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
    gap: 1.2rem;
    margin: 0 auto 1rem;
}
.social-links{
    display: flex;
    justify-content: center;
    gap: 1rem;
    transition: var(--transition);
}
.social-links a{
    display: flex;
    margin-bottom: 2rem;
    padding: 0.9rem;
    background-color: var(--color-bg);
    color: var(--color-primary);
    border-radius: 0.7rem;
    border: 1px solid transparent;
    font-size: 1.3rem;
}
.social-links a:hover{
    background-color: transparent;
    color: var(--color-bg);
    border-color: var(--color-bg);
    transform: scale(.8);
}
.copy-rights{
    margin-bottom: 3rem;
    color: var(--color-bg);
    font-size: 1rem;
}

/* ######### Media Queries (Small) */
@media screen and (max-width: 600px){
    .links{
        flex-direction: column;
        align-items: center;
        gap: 1.5rem;
    }
    .social-links{
        margin-bottom: 2.6rem;
    }
}