.about-container{
    display: grid;
    grid-template-columns: 30% 50%;
    gap: 20%;
    /* margin-left: 110px; */

}

.about-image{
    width: 100%;
    height: 100%;
    background:linear-gradient(45deg,transparent,var(--color-primary),transparent);
    border-radius: 40px;
    transition: var(--transition);
    display: grid;
    place-items: center;
}
.about-image img{
    border-radius: 40px;
    transform: rotate(10deg);
    width: 100%;
    height: 100%;
}
.about-image :hover{
    transform: rotate(0deg);
}

.about-cards{
    display: flex;
    justify-content: space-evenly;
    gap: 20px;
}
.about-card{
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    align-content: center;
    gap: 10px;
    padding: 1.5rem 4.5rem ;
    background-color: var(--color-bg-variant);
    border: 1px solid transparent;
    border-radius: 20px;
    text-align: center;
    transition: var(--transition);
    width: calc((100%/3));
    /* height: calc(100%/3); */
}
.about-card:hover{
    border-color:var(--color-bg-variant) ;
    background-color: transparent;
}
.about-icon{
    color: var(--color-primary);
    font-size: 20px;
    margin: 6px;
    min-width:20px ;
    min-height:20px ;
}
.about-card h5{
    font-size: 16px;
}
.about-card small{
    font-size: 10px;
    color: var(--color-light);
}
.about-content p{
    margin: 3rem 0 1.5rem;
    width: 100%;
    font-size: 13px;
    line-height: 1.8;
    color: var(--color-light);
}

/* ######### Media Queries (Medium) */
@media screen and (max-width: 1024px){
    .about-container{
        grid-template-columns: 1fr;
        gap: 6rem;
        /* margin-left: 30px; */
    }
    .about-image{
        width: 50%;
        margin: 2rem auto 4rem;
    }
    .about-content p{
        width: 60%;
    }
}
/* ######### Media Queries (Small) */
@media screen and (max-width: 600px){
    .about-container{
        gap: 4rem;
    }
    .about-image{
        width: 80%;
        margin: 0 auto 2rem;
    }
    .about-cards{
        flex-direction: column;
        place-items: center;
    }
    .about-card{
        width: 80%;
    }
    .about-content p{
        /* width: calc(100%-20%); */
        width: 80%;
        margin: 1rem auto 1.5rem;
    }
}
