.services-container{
    display: flex;
    justify-content: space-between;
    gap: 2rem;
}
.service{
    background-color: var(--color-bg-variant);
    height: fit-content;
    border-radius: 0 0 2rem 2rem;
    border: 1px solid var(--color-primary);
    transition: var(--transition);
}
.service:hover{
    background-color: transparent;
    border-color: var(--color-primary-veriant);
}
.text{
    background-color: var(--color-primary);
    padding: 2rem;
    border-radius: 0 0 2rem 2rem;
}
.text h3{
    color: var(--color-bg);
    font-size: 1rem;
    text-align: center;
}
ul{
    padding: 2rem;
}
ul li{
    display: flex;
    gap: 1rem;
    margin-bottom: .7rem;
}
.icon{
    margin-top: 2px;
    color: var(--color-primary);
    font-size: 16px;
    min-width: 16px;
}
ul li p{
    font-size: .8rem;
}

/* ######### Media Queries (Medium) */
@media screen and (max-width: 1024px){
 .services-container{
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 2rem;
 }
 .service{
    height: auto;
 }
}
/* ######### Media Queries (Small) */
@media screen and (max-width: 600px){
    .services-container{
        display: grid;
        grid-template-columns: 1fr;
        gap: 1.5rem;
        max-width: 100%;
    }
}