header{
    height: 100vh;
    overflow: hidden;
    text-align: center;
    padding-top: 60px;
}
.header-container{
    height: 100%;
    position: relative;
    text-align: center;
}
/* ##### CTA #### */
.Cta{
    display: flex;
    justify-content: center;
    gap: 15px;
    margin: 20px auto;
}

/* ###### SOCIAL ####### */

.header-social{
    display: flex;
    flex-direction: column;
    align-items: center;
    position: absolute;
    left: 0;
    bottom: 50px;
}

.header-social a{
    padding: 8px;
}

.header-social::after{
    content: "";
    height: 35px;
    width: 3px;
    background-color: var(--color-primary);
}

/* ########  ME  ####### */
.me{
    background: linear-gradient(var(--color-primary), transparent);
    position: absolute;
    left: 50%;
    transform: translatex(-50%);
    margin-top: 40px;
    padding: 25px 10px 10px 10px;
    border-radius: 12rem 12rem 0 0;
    width: 13rem;
    height: 20rem;
    overflow: hidden;
}

/* ########### Scroll down ############ */
.scroll-down{
    position: absolute;
    right: -30px;
    bottom: 70px;
    transform: rotate(90deg);
    font-weight: 200;
    font-size: 13px;
}

/* ######### Media Queries (Medium) */
@media screen and (max-width: 1024px){
    header{
        height: 68vh;
    }
}
/* ######### Media Queries (Small) */
@media screen and (max-width: 600px){
    header{
        height: 100vh;
    }
    .scroll-down, .header-social{
        display: none;
    }
    .me{
        width: 22rem;
        height: 27rem;
    }
}