nav{
    width: fit-content;
    background-color: rgba(0,0,0,0.5);
    padding: 7px 30px;
    border-radius: 20px;
    z-index: 2;
    position: fixed;
    bottom: 10px;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    gap:15px;
}
nav a{
    background-color: transparent;
    text-align: center;
    color:var(--color-light);
    border-radius: 50%;
    font-size: 20px;
    padding: 8px;
    transition: var(--transition);
}
nav a:hover{
    background-color: var(--color-primary);
    color: rgba(0,0,0,3) ;

}
nav a.active{
    background-color: var(--color-primary);
    color: rgba(0,0,0,3) ;
}

/* ######### Media Queries (Small) */
@media screen and (max-width: 600px){
    nav{
        display: none;
    }
}